import { request } from "@/utils/request";
import { REQUEST_SUCCEEDED_CODE } from '@/common/constants';
/**
 * boxTickets 我的盲盒劵
 * @description 我的盲盒劵
 * @Author: 穆兰
 * @Date: 2022/1/10
 * @LastEditors: 穆兰
 * @LastEditTime: 2022/1/10
 */
export default {
    data() {
        return {
            boxTicketsAvailableSum: 0,
            tickets: [],
            total: 0,
            pageNumber: 1
        };
    },
    onLoad() {
        this.getBoxTicketsInfo(this.pageNumber);
    },
    methods: {
        /**
         *@description 点击返回按钮
         */
        onNarLeftClick() {
            uni.navigateBack({
                delta: 1
            });
        },
        /**
         * @description 页数改变时
         */
        onPageChange(e) {
            this.pageNumber = e.current;
            this.getBoxTicketsInfo(this.pageNumber);
        },
        /**
         * @description  获取我拥有的盲盒劵的信息
         * @param {number} page 页号
         */
        async getBoxTicketsInfo(page) {
            let res = await request({
                data: {
                    method: 'GET',
                    group: 'blindBox',
                    action: 'ticket/list',
                    data: {
                        page: page
                    },
                    header: {
                        'content-type': 'application/x-www-form-urlencoded',
                    },
                }
            });
            if (res.data.code === REQUEST_SUCCEEDED_CODE) {
                let ticketsResult = res.data.data;
                this.boxTicketsAvailableSum = ticketsResult.list.availableSum,
                    this.tickets = ticketsResult.list.tickets,
                    this.total = ticketsResult.total;
            }
            else {
                console.log('getBoxTicketsInfo', res.data);
            }
        }
    }
};
